<script>
	export default {
		data() {
			return {
				info:{}
			}
		},
		// mounted() {
		// 	this.initData();
		// },
		onShow(){
			this.initData();
		},
		methods: {
			initData:function(){
				uni.$api.getUserMoneyCountInfo().then(res =>{
					this.info = res
				})
			},
			uiClose: function() {
				uni.navigateBack()
			},
			goToRecord: function() {
				uni.navigateTo({
					url: "assets/balance"
				})
			},
			gotoBreakRecord: function() {
				uni.navigateTo({
					url: "assets/balance_break"
				})
			},
		}
	}
</script>

<style>
	.action.middle {
		position: absolute;
		left: 0;
		right: 0;
	}

	.color-bg {
		width: 100%;
		height: 976rpx;
		background: linear-gradient(180deg, #146EE6 0%, #227CE7 26%, #5DC3E6 57%, #F6F6F6 100%);
		position: absolute;
		left: 0;
		top: 0;
		z-index: 0;
	}

	.container {
		padding: 36rpx 32rpx;
		position: relative;
		z-index: 1;
	}

	.more-cold {
		position: absolute;
		padding: 6rpx 8rpx;
		right: -44rpx;
		top: 0;
	}

	.operation-btn {
		width: 212rpx;
		height: 90rpx;
		border-radius: 12rpx;
		background: #fff;
		font-size: 32rpx;
		line-height: 32rpx;
	}

	.operation-btn.shallow {
		background-color: rgba(255, 255, 255, 0.2);
	}

	.profit-box {
		width: 100%;
		background-color: #fff;
		border-radius: 20rpx;
		padding: 32rpx 0;
	}

	.profit-info {
		width: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
